import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/observable';
import { ConfigService } from '../config.service';

@Injectable()
export class HospitalService {

  constructor(
    private httpClient: HttpClient,
    private configService: ConfigService) { }


  public get_hospitals_all(): Observable<any[]> {
    return this.httpClient
      .get<any[]>(`${this.configService.url_base}/hospital/all`);
  }

  public getHospitalById(id): Observable<any>{
    return this.httpClient
      .get<any[]>(`${this.configService.url_base}/hospital/getId/${id}`);
  }

  public getDoctorInHospital(id): Observable<any>{
    return this.httpClient
      .get<any[]>(`${this.configService.url_base}/doctor/inHospital/${id}`);
  }
}



