import {Component, OnInit} from '@angular/core';
import {DoctorService} from './doctor.service';
import {BsModalService} from 'ngx-bootstrap/modal';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {RecordService} from "../record/record.service";
import {DatePipe} from '@angular/common';
import {ConfigService} from "../config.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-doctor',
  templateUrl: './doctor.component.html',
  styleUrls: ['./doctor.component.css']
})


export class DoctorComponent implements OnInit {
  url_base: any;
  doctors: any;
  modalRef: BsModalRef;
  alertBool: boolean;

  doctor_id: any;
  date_records: any;
  time_records: any;

  select_date: any;
  select_time: any;

  search: string = "";
  gender: string = "";
  searchHospital: string = "";

  constructor(private doctorService: DoctorService,
              private modalService: BsModalService,
              private recordService: RecordService,
              private configService: ConfigService,
              private datePipe: DatePipe,
              private router: Router) {

    this.url_base = this.configService.url_base;
    this.doctors = [];
    this.date_records = [];
    this.time_records = [];
    this.select_date = null;
    this.select_time = null;

    this.alertBool = false;
  }

  public ngOnInit() {
    this.getDoctors();

  }

  public getDoctors() {
    this.doctorService
      .get_doctor_all()
      .subscribe(res => this.doctors = res);
  }


  modalRecordAdd(template, id) {
    this.doctor_id = id;
    this.modalRef = this.modalService.show(template);
    this.generateDates();
    this.select_date = null;
    this.select_time = null;
  }

  addRecord(modalRef) {
    if (this.elem_sel_date !== null && this.elem_sel_time !== null) {
      this.alertBool = false;
      this.recordService
        .addRecord(this.doctor_id,
          this.datePipe.transform(this.select_date, 'dd-MM-yyyy') + ' ' + this.datePipe.transform(this.select_time, 'HH:mm'))
        .subscribe(res => {
          this.recordService.getHttpUserRecords();
          modalRef.hide();
        });
    } else {
      this.alertBool = true;
    }

  }

  generateDates() {
    this.alertBool = false;
    this.date_records = [];
    this.time_records = [];
    this.elem_sel_date = null;
    this.elem_sel_time = null;

    var dd = new Date();
    for (var i = 0; i < 30; i++) {
      var dt = new Date(dd.setDate(dd.getDate() + 1));
      if (dt.getDay() != 0)
        this.date_records.push(dt)
    }

    dd.setHours(8);
    dd.setMinutes(0);

    for (var i = 0; i < 18; i++) {
      var dt = new Date(dd.setMinutes(dd.getMinutes() + 30));
      this.time_records.push(dt)
      dd.setMinutes(dd.getMinutes() + 10)
    }

  }

  /** onClick */
  elem_sel_date: any = null;

  onClickDate($event, rec): void {
    if (this.elem_sel_date !== null) {
      this.elem_sel_date.target.className = "";
    }
    this.elem_sel_date = $event;
    this.elem_sel_date.target.className = "sel";

    this.select_date = rec;

    if (this.elem_sel_date !== null && this.elem_sel_time !== null) {
      this.alertBool = false;
    }
  }

  elem_sel_time: any = null;

  onClickTime($event, rec): void {
    if (this.elem_sel_time !== null) {
      this.elem_sel_time.target.className = "";
    }
    this.elem_sel_time = $event;
    this.elem_sel_time.target.className = "sel";

    this.select_time = rec;

    if (this.elem_sel_date !== null && this.elem_sel_time !== null) {
      this.alertBool = false;
    }
  }

  /**End onClick */

  public onRouteDoctorId(id) {
    this.router.navigate(['doctor/' + id])
  }

  public onRouteHospitaId(id) {
    this.router.navigate(['hospital/' + id])
  }
}





