import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs/observable';
import {ConfigService} from '../config.service';

@Injectable()
export class DoctorService {
  url_doctor_all = `doctor/all`;
  doctors: any;

  constructor(private httpClient: HttpClient,
              private configService: ConfigService) {
    this.doctors = [];
  }


  public get_doctor_all(): Observable<any[]> {
    return this.httpClient
      .get<any[]>(`${this.configService.url_base}/${this.url_doctor_all}`);
  }

  public getDoctorId(id): Observable<any[]> {
    return this.httpClient
      .get<any[]>(`${this.configService.url_base}/doctor/get/${id}`);
  }

}



