import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {HospitalService} from "../hospital/hospital.service";

@Component({
  selector: 'app-hospital-page',
  templateUrl: './hospital-page.component.html',
  styleUrls: ['./hospital-page.component.css']
})
export class HospitalPageComponent implements OnInit {
  public hospital: any;
  public hospital_type: any;
  public doctors: any;

  constructor(private route: ActivatedRoute,
              private hospitlService: HospitalService) {

  }


  ngOnInit() {
    this.route.params.subscribe(params => this.loadPage(params.id));
  }


  public loadPage(id) {
    this.getHospital(id);
  }

  public getHospital(id) {
    this.hospitlService.getHospitalById(id).subscribe(
      res => {
        this.hospital = res['hospital'];
        this.hospital_type = res['hospital'];
      }
    );

    this.hospitlService.getDoctorInHospital(id).subscribe(
      res => this.doctors = res,
    );
  }
}



