import {BrowserModule} from '@angular/platform-browser';
import {LOCALE_ID, NgModule} from '@angular/core';

import {RouterModule, Routes, CanActivate} from '@angular/router';
import {HttpClientModule} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { DatePipe } from '@angular/common';
import {CollapseModule, BsDropdownModule, ModalModule} from 'ngx-bootstrap';

import localeUk from '@angular/common/locales/uk';
import {registerLocaleData} from '@angular/common';

registerLocaleData(localeUk, 'uk');

import { AgmCoreModule } from '@agm/core';

import {ConfigService} from './config.service';
import {HospitalService} from './hospital/hospital.service';
import {DoctorService} from './doctor/doctor.service';
import {RegisterService} from './register/register.service';
import {AuthService} from './auth/auth.service';
import {UserService} from './user/user.service'
import {RecordService} from "./record/record.service";
import {MedcardService} from "./medcard/medcard.service";
import {AuthGuard} from './auth/auth.guard';


import {AppComponent} from './app.component';
import {AuthComponent} from './auth/auth.component';
import {RegisterComponent} from './register/register.component';
import {DoctorComponent} from './doctor/doctor.component';
import {HospitalComponent} from './hospital/hospital.component';
import {UserComponent} from './user/user.component';
import {CardComponent as UserCardComponent} from './user/card/card.component';
import {InfoComponent as MedCardInfoComponent} from './medcard/info/info.component';
import {ListComponent} from './record/list/list.component';
import {ComplaintsComponent} from './medcard/complaints/complaints.component';
import {IndexeComponent} from './medcard/indexe/indexe.component';
import { HospitalPageComponent } from './hospital-page/hospital-page.component';
import { DoctorPageComponent } from './doctor-page/doctor-page.component';
import { UserPageSettingComponent } from './user-page-setting/user-page-setting.component';
import { UserPageRecordsComponent } from './user-page-records/user-page-records.component';
import { UserFilterPipe } from './doctor/user-filter.pipe';
import { DocrorFilterPipe } from './doctor/docror-filter.pipe';
import { DoctorGenderFilterPipe } from './doctor/doctor-gender-filter.pipe';
import { DoctorHospitalNameFilterPipe } from './doctor/doctor-hospital-name-filter.pipe';
import { HospitalNameFilterPipe } from './hospital/hospital-name-filter.pipe';

const appRoutes: Routes = [
  {path: 'auth', component: AuthComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'doctor', component: DoctorComponent},
  {path: 'doctor/:id', component: DoctorPageComponent},
  {path: 'hospital', component: HospitalComponent},
  {path: 'hospital/:id', component: HospitalPageComponent},
  {path: 'user', component: UserComponent, canActivate: [AuthGuard]},
  {path: 'user/setting', component: UserPageSettingComponent, canActivate: [AuthGuard]},
  {path: 'user/records', component: UserPageRecordsComponent, canActivate: [AuthGuard]},
  {path: '', redirectTo: '/register', pathMatch: 'full'},
  {path: '**', redirectTo: '/auth'}
];


@NgModule({
  declarations: [
    AppComponent,
    AuthComponent,
    RegisterComponent,
    DoctorComponent,
    HospitalComponent,
    UserComponent,
    UserCardComponent,
    MedCardInfoComponent,
    ListComponent,
    ComplaintsComponent,
    IndexeComponent,
    HospitalPageComponent,
    DoctorPageComponent,
    UserPageSettingComponent,
    UserPageRecordsComponent,
    UserFilterPipe,
    DocrorFilterPipe,
    DoctorGenderFilterPipe,
    DoctorHospitalNameFilterPipe,
    HospitalNameFilterPipe,
  ],
  imports: [
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCxjZvF5tB0ZTx_5IIBf2j_90DV_CnHOvI'
    }),
    FormsModule,
    CollapseModule.forRoot(),
    BsDropdownModule.forRoot(),
    ModalModule.forRoot(),
    BrowserModule,
    HttpClientModule,
    ReactiveFormsModule,
    RouterModule.forRoot(
      appRoutes,
      {enableTracing: true} // <-- debugging purposes only
    ),
  ],
  providers: [
    ConfigService,
    HospitalService,
    DoctorService,
    RegisterService,
    AuthService,
    UserService,
    RecordService,
    MedcardService,
    AuthGuard,
    DatePipe,
    {provide: LOCALE_ID, useValue: 'uk'}
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    UserCardComponent,
    MedCardInfoComponent,
    ListComponent,
    ComplaintsComponent,
    IndexeComponent,],
})
export class AppModule {
}



