import { Component, OnInit } from '@angular/core';
import {MedcardService} from "../medcard.service";
import {BsModalRef} from "ngx-bootstrap/modal/bs-modal-ref.service";
import {BsModalService} from "ngx-bootstrap/modal";

@Component({
  selector: 'medcard-indexes',
  templateUrl: './indexe.component.html',
  styleUrls: ['./indexe.component.css']
})
export class IndexeComponent implements OnInit {
  modalRef: BsModalRef;

  constructor(private medcard:MedcardService,
              private modalService: BsModalService) { }

  ngOnInit() {}

  modal_add(template: any) {
    this.modalRef = this.modalService.show(template);
  }

}



