import {Injectable} from '@angular/core';
import {AuthService, AuthStatus} from "../auth/auth.service";
import {HttpClient} from "@angular/common/http";
import {BehaviorSubject} from "rxjs/BehaviorSubject";
import {ConfigService} from "../config.service";
import {Observable} from "rxjs/Observable";

@Injectable()
export class MedcardService {

  public authStatus: AuthStatus;
  public medcardInfo = new BehaviorSubject([]);

  constructor(private authService: AuthService,
              private httpClient: HttpClient,
              private configService: ConfigService) {
    this.authService.getAuthStatus().subscribe(
      res => {
        this.authStatus = res;
        this.onInit();
      }
    );
  }

  private onInit() {
    this.getServerMedcardInfo();
  }

  private getServerMedcardInfo() {
    this.httpClient
      .post<any[]>(`${this.configService.url_base}/medcard/info/get`,
        `access_token=${this.authStatus.access_token}`, {
          headers: {
            'content': 'application/json',
            'content-type': 'application/x-www-form-urlencoded'
          }
        }).subscribe(
      res => {
        this.medcardInfo.next(res['MedcardInfo']);
        console.log(res);
      },
      err => console.log(err)
    );
  }


  public getMedcardInfo() {
    return this.medcardInfo.asObservable();
  }

  public setServerMedcardInfo(data:any): Observable<any> {
    return this.httpClient
      .post<any[]>(`${this.configService.url_base}/medcard/info/set`,
        `access_token=${this.authStatus.access_token}&blood_type=${data['blood_type']}&` +
        `physical_injury=${data['physical_injury']}&main_diagnosis=${data['main_diagnosis']}&` +
        `allergic_history=${data['allergic_history']}&drug_intolerance=${data['drug_intolerance']}&`,
        {
          headers: {
            'content': 'application/json',
            'content-type': 'application/x-www-form-urlencoded'
          }
        });
  }
}



