import {Component, OnInit} from '@angular/core';
import {RecordService} from "../record.service";
import {BsModalService} from 'ngx-bootstrap/modal';
import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';
import {ConfigService} from "../../config.service";

@Component({
  selector: 'record-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  public url_base: string;
  public records: any;
  public select_record_id: any;
  record_info:any = [];

  constructor(private recordService: RecordService,
              private modalService: BsModalService,
              private conf:ConfigService) {
      this.url_base = this.conf.url_base;
  }

  ngOnInit() {
    this.recordService.records.subscribe(res => this.records = res);
  }


  public del() {
    this.recordService
      .delRecord(this.select_record_id)
      .subscribe(res => {this.recordService.getHttpUserRecords()});

  }

  public inf(id) {}

  modalRef: BsModalRef;

  modal_del(id, template: any) {
    this.select_record_id = id;
    this.modalRef = this.modalService.show(template);
  }

  modalMoreInfoShow(doc:any, template: any){
    this.record_info = doc;
    this.modalRef = this.modalService.show(template);
  }
}



